body {
  background-color: whitesmoke;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body #root {
  display: grid;
  grid-template-rows: calc(2em + 6vmin) 1fr calc(2em + 6vmin);
}

main {
  /* background-color: lightgrey; */
  grid-row: 2;
}

.home {
  display: flex;
  margin: auto;
  flex-direction: column;
}

.home-div {
  display: flex;
  flex-direction: column;
  position: relative;
}

.computer-div {
  position: relative;
}

.pc-icon {
  width: 100%;
}

.computer-photo {
  background-image: url('https://avatars.githubusercontent.com/u/85768828?v=4');
  background-position: center;
  background-size: cover;
  border-radius: 50%;
  box-shadow: 0 0 2px 0.3px black;
  height: 37%;
  left: 31.5%;
  position: absolute;
  width: 37%;
  top: 12%;
}

.sociallinks-div {
  display: flex;
  position: absolute;
  justify-content: center;
  margin: auto;
  top: 45%;
  width: 100%;
}

.sociallinks-div > :nth-child( 1 ) {
  animation: IconPopUp 3s infinite;
}

.sociallinks-div > :nth-child( 2 ) {
  animation: IconPopUp 7s infinite;
}

.sociallinks-div > :nth-child( 3 ) {
  animation: IconPopUp 5s infinite;
}

.social-icons {
  margin: 1em;
  width: 35px;
}

.keyboard-icon {
  margin: -10% auto;
  transform: scaleY(0.8);
  width: 75%;
}

.about-text {
  font-size: 1.2em;
  letter-spacing: 0.05em;
  line-height: 1.3em;
  max-width: 70%;
  padding: 5px 10px;
}

@keyframes IconPopUp {
  85% { transform: none; }
  90% { transform: scale(1.1); }
}